:root {

  --bg-button: #cbc0d3;
  --border-button: solid 1px #ffffff2d;
  --bg-buttonOperator: #ef233ccc;
}

.button {
  font-size: 1.4em;
  background-color: var(--bg-button);
  border: none;
  border-right: var(--border-button);
  border-bottom: var(--border-button);
  outline: none;
}

.button:active {
  background-color: #d0d1ff;
}

.button.double {
  grid-column: span 2;
}

.button.triple {
  grid-column: span 3;
}

.button.operation {
  background-color: var(--bg-buttonOperator);
  color: #f0f0f0;
}

.button.operation:active {
  background-color: #f58075;
}
